import React from 'react'
import './Private.css'

class PrivatePage extends React.Component {
    constructor(params){
        super(params)
        const token = localStorage.getItem('token');
        console.log('token =>', token)
        if(token === null) {
            window.location = 'https://singhealthid.azurewebsites.net/connect/token'
        }
    }
    // componentWillMount() {
    // }
    // componentDidMount() {
    //     const token = localStorage.getItem('token');
    //     console.log('token cdm =>', token)
    //     if(token === '') {
    //         window.location = 'https://singhealthid.azurewebsites.net/connect/token'
    //     }
    // }
    render() {
        const token = localStorage.getItem('token');
        console.log('token =>', token)
        var hide = null
        if(token === null) {
            hide = (<div><h1>Unauthorized!</h1></div>)
        }else {
            hide = (<div><h1>You are authorized. This is a private page</h1></div>)
        }
        return (<div>{hide}</div>)
    }

}

export default PrivatePage;