import React from 'react';
import './App.css';
import { Switch, Route } from 'react-router-dom';
import Home from './pages/HomePage'
import Private from './pages/PrivatePage'

function App() {
  return (
    <div className="App">
    <Switch>
      <Route path="/private" component={Private} />
      <Route path="/" component={Home} />
    </Switch>
    </div>
  );
}

export default App;
